const app = getApp();
export default {
    components: {
    },
    data() {
        return {
            count: 60,
            mobile_number: '',
            token: '',
            btn_txt: '获取验证码',
            show_weixin: true ,
            code : '' ,
            module : 'bra' ,
            page_name : 'bra_user_chang_password' ,
            is_page_loaded : false ,
            page_data : {} ,
            pass : ''
        };
    },
    onLoad() {
    },
    onShow() {
        this.onMhcmsPull();
    },

    methods: {
        onMhcmsPull: function () {
            let params = {};
            this.bra_common_page(this.module, this.page_name, params, true, (data) => {
                console.log(data);
                this.page_data = this.elements.page_data;
                if(this.page_data && this.page_data.user){
                    this.is_page_loaded = true;
                    this.mobile_number = this.page_data.user.mobile;
                }
                uni.stopPullDownRefresh();
            });
        },
        recount: function () {
            var that = this;
            if (that.count > 0) {
                setTimeout(function () {
                    that.count--;
                    that.recount()
                }, 1000);
                this.btn_txt = that.count;
            } else {
                that.count = 60;
                this.btn_txt = "获取验证码";
            }
        },
        send_sms: function (mobile) {
            let action = this.bra_service('bra', 'send_login_verify_code');
            var that = this;
            let params = {mobile: that.mobile_number};
            params.is_mixed = 2;
            let promise = this.fetch(action, params , 'post');
            promise.then((data) => {
                uni.showToast({
                    icon: "none",
                    title: '已经发送',
                    duration: 2000
                });
                this.recount();
            }, error => {
                setTimeout(function () {
                    uni.showToast({
                        icon: "none",
                        title: error.msg,
                        duration: 2000
                    });
                } , 100);

            })
        },
        send_verify_code() {
            this.send_sms(this.mobile_number);
        } ,
        update_field : function () {
            if(this.code.length < 4){
                return uni.showToast({
                    icon : "none",
                    title:  '请输入正确的验证码',
                    duration: 2000
                });
            }

            let params = {};
            params.pass = this.pass;
            params.code = this.code;
            this.bra_common_page(this.module, this.page_name, params, true, (data) => {
                console.log(data);
                var page_data = data.data.page_data;
                this.show_toast(page_data.msg);
                if(page_data.code == 1){
                    this.$store.commit('change_login', false);
                    this.onNav({
                        url : '/pages/hotel/user/index' ,
                        type: 'switch'
                    });
                }

                uni.stopPullDownRefresh();
            } , 'post' , false);
        }
    }
}
